/* 全局样式 */
* {
    margin: 0;
    padding: 0;
}

#root {
    width: 100%;
    /* height: 100%; */
    overflow-x: hidden;
    position: relative;
}

html {
    font-size: 14px;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

.home,
html,
body {
    height: 100%;
}