/* aside */

.left_col {
    z-index: 99999;
}

.aside_auto {
    height: 100%;
    transition: all .3s ease-in-out;
}

@media screen and (max-width: 768px) {

    .home_row {
        height: 100%;
    }

    .left_col {
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        width: 50%;
    }

    .aside_auto {
        width: 0%;
        overflow: hidden;
        position: relative;
    }

    .asideOutWidth .topNav {
        display: inline-block
    }

}

@media screen and (min-width: 768px) {
    .aside_auto {
        width: 100% !important;
    }
}

.topNav {
    display: none;
    position: absolute;
    top: 10px;
    left: 15px;
    cursor: pointer;
    transition: all .3s ease-in-out;
    color: #000;
    font-size: 1.3rem;
}

.asideOutWidth {
    height: 100%;
    position: relative;
}

.aside {
    width: 100%;
    padding: 20px 30px 0 30px;
    text-align: center;
    position: absolute;
    left: 0;
    top: 0;
}

.aside h2 {
    font-size: 2rem;
    font-weight: bold;
    color: #fff;
    margin: 0 0 20px 0;
}

.head img {
    width: 60%;
    border-radius: 50%;
}

.desc_me,
.desc_me a {
    color: #fff;
}

.desc_me {
    padding: 20px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.aside .aside_bottom ul {
    /* display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 50%; */
    margin-top: 40px
}

.aside .aside_bottom ul li {
    margin: 10px;
}

.aside .aside_bottom ul li,
.aside .aside_bottom ul li a {
    font-weight: bold;
    color: #DFF5E3
}

.aside .aside_bottom ul li a {
    margin-left: 10px;
}

.active {
    color: #2F6538 !important
}

.getTouch {
    margin-top: 20px;
    width: 50%;
    height: 40px;
    border-radius: 5px;
    background: #428e4f6b;
    color: #fff;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.aside button:hover {
    background: #296834;
}

/* backgroundChange */
.backgroundChange {
    position: absolute;
    top: 65px;
    right: 0;
    transition: all .5s ease-in-out;
}

.backgroundChange span {
    color: #fff;
    font-weight: bold;
}

.backgroundChange ul {
    margin-top: 10px;

}

.backgroundChange>div {
    background: #223142;
    padding: 10px 30px 10px 10px;
}

.backgroundChange .icon {
    font-size: 1.5rem;
    background: #223142;
    color: #FFFFFF;
    width: 33px;
    height: 33px;
    position: absolute;
    left: -33px;
    top: 17px;
    border-radius: 5px 0px 0px 5px;
    padding: 6px;
    cursor: pointer;
}

.choose_bg {
    display: flex;
    flex-wrap: wrap;
}

.choose_bg li {
    width: 20%;
    margin: 2%;
}

.choose_bg li .choose_btn {
    width: 100%;
    height: 100%;
    border: 1px solid #fff;
    cursor: pointer;
}

.choose_bg li .choose_btn:hover {
    filter: brightness(90%)
}

.close_choose {
    position: absolute;
    right: 3px;
    top: 8px;
    font-size: 1.5rem;
    color: #9198A1 !important;
    cursor: pointer;
}

/* publicBlog */

.publicBlog_col {
    width: 100%;
}

.publicBlog {
    width: 100%;
    float: right;
    background: #FAFAFA;
    padding: 40px 0;
}

.publicBlog .publicBlog_outer {
    width: 60%;
    text-align: center;
    margin: 0 auto;
}

@media screen and (max-width: 1200px) {
    .publicBlog .publicBlog_outer {
        width: 100%;
    }
}

.publicBlog_outer>h1 {
    font-weight: bold;
    font-size: 2.2rem;
}

.publicBlog_outer>p {
    color: #4f4f4f;
    margin: 15px 0;
}

.input_search {
    width: 60%;
}

.input_search button {
    background: #5FCB71;
    border-color: #5FCB71;
}

.input_search button:hover,
.input_search button:focus {
    background: #296834;
    border-color: #296834;
}

.container_blog {
    width: 100%;
    padding: 20px 0;
}

.list_item {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 50px auto;
}

@media screen and (max-width: 1130px) {
    .list_item {
        width: 90%;
    }
}

@media screen and (max-width: 875px) {
    .list_item>.blog_img {
        display: none;
    }
}

.blog_img img {
    max-width: 100%;
}

.blog_img {
    margin-right: 15px;
}

.blog_right h2 {
    font-weight: bold;
    margin: 0;
}

.comments {
    font-size: .8rem;
    color: #8f8f8f;
    margin: 5px 0;
}

.content {
    margin: 0;
}

.blog_right a {
    color: #5FCB71;
}

/* about */
/* .about_outer {
    float: right;
    width: 80%;
} */

.aboutList {
    padding: 5% 17% 0 17%;
}

.aboutItem div img {
    width: 100%;
}

.aboutList .aboutItem:not(:first-child) h2 {
    font-weight: bold;
    margin-top: 40px;
}

.aboutList .aboutItem:first-child h2 {
    font-size: 2.9rem;
    font-weight: bold;
}